

var table
$(() => {
    table = $('#example').DataTable({
        dom: 'Bfrtip',
        responsive: true,
        ajax: {
            url: '/articles',
        },
        language: {
            url: '/js/es-MX.json'
        },
        buttons: [
            {
                text: '<i class="fa fa-file-excel"></i> Excel',
                className: 'btn btn-success',
                extends: 'excelHtml5',
            },
            'pdfHtml5'
        ],
        columns: [
            { 
                data: 'num',
                title: '#',
                render : function(data, type, row, meta) {
                    return `${meta.row + 1}`
                }
            },
            {
                data: 'id'
            },
            {
                data: 'name',
    
            },
            {
                data: 'measure',
            },
            {
                data: 'price',
            },
            {
                data: 'type',
            },
        ],
    });
})